.home {
  height: 100vh;
  background-image: linear-gradient(to bottom right, #74ebd5, #9face6);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about {
  text-align: center;
  max-width: 800px;
  padding: 20px;
  background-color: #eddddd;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  -webkit-animation: fadein 5s;
          animation: fadein 5s;
}

.about h2 {
  font-size: 3.5rem;
  margin-bottom: 20px;
  -webkit-animation: typing 2s steps(15, end) 1s forwards;
          animation: typing 2s steps(15, end) 1s forwards;
}

.about p {
  font-size: 1.5rem;
  color: #555555;
  margin: 0;
  opacity: 0;
  -webkit-animation: fadein 3s 4s forwards;
          animation: fadein 3s 4s forwards;
}

.about p::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #555555;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation: circle 2s 5s forwards;
          animation: circle 2s 5s forwards;
}

.icons-all {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.icons-all a {
  display: inline-block;
  margin: 0 10px;
  border-radius: 50%;
  padding: 10px;
  transition: background-color 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, background-color 0.3s;
  transition: transform 0.3s, background-color 0.3s, -webkit-transform 0.3s;
}

.icons-all a:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  background-color: #ffffff;
}

.icons-all svg {
  font-size: 2.5rem;
  color: #555555;
}

@media only screen and (max-width: 600px) {
  .about h2 {
    font-size: 2.5rem;
  }

  .about p {
    font-size: 1rem;
  }

  .icons-all svg {
    font-size: 1.5rem;
  }
}

/* Animations */

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-webkit-keyframes circle {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes circle {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

