.blog-container {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 64px); /* set the height to the full viewport height minus the height of the navigation bar */
  margin-top: 5%; /* set the top margin to the height of the navigation bar */
}

.blog-post img {
  max-width: 100%;
  height: auto;
}

.active-post {
  opacity: 1; /* set opacity to 1 to remove the transparency */
}

.active-post img {
  max-width: 50%; /* adjust the max-width as desired */
  height: auto;
}

.blog-post {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  margin-bottom: 30px;
  overflow: hidden;
  padding: 20px;
  width: calc(33.33% - 10px);
  transition: all 0.3s ease-in-out;
  margin: 0 10px 30px;
}

.blog-post:hover {
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.blog-post h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.blog-post p {
  font-size: 1rem;
  margin-bottom: 20px;
}

.blog-post img {
  display: block;
  height: 100%;
  margin-bottom: 10%;
  width: 100%;
}

.active-post {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  max-width: 800px;
  overflow: hidden;
  padding: 20px;
}

.active-post h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.active-post p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.active-post img {
  display: block;
  height: 300px;
  margin: 20px auto;
  width: 100%;
}

.active-post button {
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 20px;
  padding: 10px;
  transition: all 0.3s ease-in-out;
}

.active-post button:hover {
  background-color: #2E8B57;
}

.active-post.open {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: all 0.3s ease-in-out;
  margin-top: 30px;
}

.full-screen-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 1;
}

.footer {
  background-color: #333;
  color: white;
  padding: 10px;
  text-align: center;
}

.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  background-color: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}

.search-container input[type="text"] {
  padding: 10px;
  width: 50%;
  max-width: 500px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}


.search-container input[type="text"]:focus {
  outline: none;
  border: 1px solid #007bff;
}

@media (max-width: 768px) {
  .blog-container {
    flex-direction: column;
  }

  .blog-post {
    width: 100%;
  }

  .active-post {
    max-width: 100%;
  }
}
